<template>
  <div>
    <back-home-btn>
      <template>
        <el-button type="primary" @click="showDialog">showVideoBox</el-button>
      </template>
    </back-home-btn>

    <el-dialog
      title="video"
      :visible.sync="dialogVisible"
      width="60%"
      :append-to-body="false"
      :modal-append-to-body="false"
      :before-close="closeDialog"
    >
      <div class="video-box">
        <div v-for="(item, index) in videoData" :key="index">
          <span> {{ item.content }} </span>
          <video
            id="video"
            :src="item.videoFlash"
            controls
            style="width: 100%; height: 550px"
            autoplay
          ></video>
        </div>
      </div>
      <span slot="footer" class="dialog-footer">
        <el-button @click="closeDialog">关 闭</el-button>
      </span>
    </el-dialog>
  </div>
</template>

<script>
export default {
  name: "videoPlayMp4",
  data() {
    return {
      dialogVisible: false,
      videoData: [],
    };
  },
  methods: {
    getVideoData() {
      this.$axios
        .create({
          baseURL: "",
          timeout: 5000,
          headers: { "Content-type": "multipart/form-data" },
        })
        .get("/videoData.json")
        .then((res) => {
          console.log(res);
          this.videoData = res.data.data;
        });
    },
    showDialog() {
      this.dialogVisible = true;
      this.getVideoData();
    },
    closeDialog() {
      this.dialogVisible = false;
    },
  },
};
</script>

<style lang="scss" scoped>
.video-box {
  height: 380px;
  overflow: auto;
}
</style>
